//icon
.icon {
  display: inline-block;
  &--link {
    width: 24px;
    height: 24px;
    background-repeat: no-repeat;
    background-size: 100% auto;
    background-position: center bottom; }
  &--link-small {
    width: 18px;
    height: 18px;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    background-position: center bottom; }
  &--link-smallest {
    width: 16px;
    height: 16px;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    background-position: center bottom; }
  &--connections {
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    margin-right: 20px; }
  &--mlauto {
    margin-left: auto; }
  &--mr12 {
    margin-right: 12px; }
  &--mr20 {
    margin-right: 20px; }
  &--quite-happy {
