//watched
.watched {
  background: $dark;
  width: 100%;
  overflow: hidden;
  padding: 10px 0 12px 0;
  &--dashboard {
    background: $gray-bg;
    margin-top: 60px; } }
.watched__gallery {
  padding: 0 6px;
  display: flex;
  justify-content: flex-start;
  align-items: center; }
.watched__control {
  flex: none;
  width: 56px;
  height: 46px;
  background: $violet;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0 6px; }
.watched__label {
  @include font(14px, $main-font, 700);
  letter-spacing: 0.6px;
  line-height: 14px;
  color: $white;
  margin-bottom: 4px; }
.message {
  background: $dark;
  border-radius: 8px;
  padding: 5.2px 17px 5.2px 8px;
  //padding: 5.2px 10px 5.2px 8px
  display: flex;
  justify-content: space-between;
  align-items: center;
  //width: 186px
  width: 181px;
  flex-basis: 186px;
  margin: 0;
  flex: none;
  &--white {
    background: $white; } }
.message__thumbnail {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 36px;
  height: 36px;
  //margin-right: 10px
  margin-right: 8px; }
.message__data {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: calc(186px - 8px - 36px - 10px - 17px); }
.message__header {
  display: flex;
  justify-content: space-between;
  flex-wrap: nowrap;
  width: 100%; }
.message__count {
  margin-bottom: 0;
  @include font(16px, $main-font, 700);
  letter-spacing: 0.6px;
  line-height: 16px;
  color: $white;
  &--dark {
    color: $dark; } }
.message__nickname {
  @include font(16px, $main-font, 400);
  line-height: 16px;
  color: $white;
  letter-spacing: 0.6px;
  margin-bottom: 0;
  &--dark {
    color: $dark; } }
.message__chanel {
  @include font(16px, $main-font, 400);
  line-height: 16px;
  color: $white;
  opacity: 0.5;
  margin-bottom: 0;
  width: 100%;
  text-align: left;
  letter-spacing: 0.8px;
  &--dark {
    color: $dark; } }
//watched END
