//header
.header {
  width: 100%;
  padding: 0;
  position: relative;
  overflow: hidden;
  background-color: $dark;
  background-repeat: no-repeat;
  background-image: url("../img/header/header_bg-1440.png");
  //height: 1200px
  height: calc(1080px - 66.4px);
  background-position: center bottom;
  background-size: 100% auto;
  margin: 0;
  @media only screen and (max-width : 1919px) {
    //height: calc(100vw / 1440 * 794)
    height: calc(100vw / 1440 * 800);
    width: 100%;
    background-size: 100% auto;
    background-color: $dark;
    background-position: center bottom; }
  @media only screen and (max-width : 767px) {
    height: 460px;
    background-size: auto 100%; }
  @media only screen and (max-width : 479px) {
    background-size: auto 100%;
    height: 520px;
    background-position: 30% bottom; }
  &--dashboard {
    background: none;
    height: auto;
    overflow: visible;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1020; } }
.header__block {
  height: calc(100% - 76px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 5;
  @media only screen and (max-width : 767px) {
    padding-bottom: 26px; } }
.login {
  margin-right: 18px;
  margin-left: 42px;
  @media only screen and (max-width : 1439px) {
    position: relative;
    transform: translateX(0);
    margin-left: 13px; } }
.language__img {
  width: 21px;
  height: 15px; }
.social-link {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100% 100%;
  &--twitter {
    background-image: url("../img/header/twitter.svg");
    &:hover, &:active, &:focus {
      background-image: url("../img/header/twitter-green.svg"); } }
  &--instagram {
    background-image: url("../img/header/instagram.svg");
    &:hover, &:active, &:focus {
      background-image: url("../img/header/instagram-green.svg"); } }
  &--facebook {
    background-image: url("../img/header/facebook.svg");
    &:hover, &:active, &:focus {
      background-image: url("../img/header/facebook-green.svg"); } }
  &--steam {
    background-image: url("../img/header/steam.svg");
    &:hover, &:active, &:focus {
      background-image: url("../img/header/steam-green.svg"); } } }
//header END
