//gift-cards
.gift-cards {
  padding-top: 82px;
  margin-bottom: 120px;
  background: $gray; }
.gift-cards__gallery {
  position: relative;
  transform: translateY(39px);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 4px;
  @media only screen and (max-width : 1279px) {
    flex-wrap: wrap;
    transform: translateY(calc(39px + 20px)); }
  @media only screen and (max-width : 991px) {
    transform: translateY(calc(39px + 20px)); }
  @media only screen and (max-width : 767px) {
    justify-content: flex-start; } }
.gift-card {
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 120px;
  height: 148px;
  background: $white;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15), 0px 6px 14px rgba(0, 0, 0, 0.1);
  @media only screen and (max-width : 1279px) {
    margin: 0 calc(((100% - (120px * 4)) /4) / 2) 20px calc(((100% - (120px * 4)) /4) / 2); }
  @media only screen and (max-width : 767px) {
    margin: 0 calc(((100% - (120px * 3)) /3) / 2) 20px calc(((100% - (120px * 3)) /3) / 2); }
  @media only screen and (max-width : 479px) {
    margin: 0 calc(((100% - (120px * 2)) /2) / 2) 20px calc(((100% - (120px * 2)) /2) / 2); } }
.gift-card-thumbnail {
  width: 120px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 109px; }
.gift-card__footer {
  background: $dark-lite;
  width: 120px;
  height: 39px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px; }
.gift-card__brand {
  @include font(16px, $main-font, 400);
  line-height: 16px;
  color: $white;
  margin-bottom: 0;
  padding-bottom: 5px;
  letter-spacing: 0.8px; }
.gift-card__img {
  &--paypal {
    width: 89.5px;
    height: 22px; }
  &--apple {
    width: 50px;
    height: 64.7px; }
  &--bitcoin {
    width: 49px;
    height: 49px; }
  &--steam {
    width: 53px;
    height: 54px; }
  &--amazon {
    width: 57px;
    height: 57px; }
  &--playstation {
    width: 49px;
    height: 76px; }
  &--ebay {
    width: 80px;
    height: 32px; }
  &--uber {
    width: 72px;
    height: 15px; } }
//gift-cards END
