//statistics
.statistics {
  //border: 1px dashed red
  //width: 100%
  width: 1160px;
  margin: 50px auto;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  &__item {
    flex-basis: calc((100% - 80px) / 3);
    max-width: calc((100% - 80px) / 3); }
  &__card {
    background: $white;
    box-shadow: 0px 2px 4px rgba(18, 33, 45, 0.1);
    border-radius: 4px;
    position: relative;
    padding: 30px 10px 15px 10px;
    //border: 1px dashed orange
    &--bg {
      &:after {
        content: "";
        position: absolute;
        display: inline-block;
        width: 118px;
        height: 98px;
        top: 40%;
        left: 50%;
        transform: translate(-50%, -50%);
        background-image: url("../img/icon/icon--statistics-bg.svg");
        background-repeat: no-repeat;
        background-size: 100% 100%;
        z-index: 0; } } }
  &__count {
    position: relative;
    z-index: 1;
    font-size: 62px;
    line-height: 74px;
    font-weight: 400;
    text-align: center;
    margin-bottom: 35px;
    letter-spacing: 3.5px;
    &--violet {
      color: $violet; }
    &--blue {
      color: $blue; }
    &--red {
      color: $red; } }
  &__text {
    position: relative;
    z-index: 1;
    text-align: center;
    margin-bottom: 0;
    font-size: 16px;
    line-height: 19px;
    font-weight: 500;
    color: $dark;
    letter-spacing: 0.8px; } }



//statistics END
