//active-campaigns
.active-campaigns {
  width: 100%;
  //width: 1160px
  //margin: 50px auto 50px
  background: $white;
  box-shadow: 0px 2px 4px rgba(18, 33, 45, 0.1);
  border-radius: 4px;
  padding: 20px 20px 18px 20px;
  &__header {
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
    padding: 8px 20px 19px 20px; }
  &__row {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 14px 20px 17px 20px;
    &:nth-child(odd) {
      background: rgba($dark, 0.06);
      border-radius: 4px; } }
  &__title {
    font-size: 16px;
    line-height: 19px;
    font-weight: 500;
    letter-spacing: 0.8px;
    margin-bottom: 0;
    &--name {
      flex-basis: 34.5%;
      max-width: 34.5%; }
    &--code {
      flex-basis: 14.5%;
      max-width: 14.5%; }
    &--data {
      flex-basis: 22%;
      max-width: 22%; }
    &--regisrations {
      flex-basis: 12%;
      max-width: 12%;
      text-align: center; }
    &--comissions {
      flex-basis: 17%;
      max-width: 17%;
      text-align: right;
      padding-right: 10px; } }
  &__text {
    font-size: 16px;
    line-height: 19px;
    font-weight: 400;
    color: $dark;
    letter-spacing: 0.8px;
    margin-bottom: 0;
    &--name {
      flex-basis: 34.5%;
      max-width: 34.5%; }
    &--code {
      flex-basis: 14.5%;
      max-width: 14.5%; }
    &--data {
      flex-basis: 22%;
      max-width: 22%; }
    &--regisrations {
      flex-basis: 12%;
      max-width: 12%;
      text-align: center; }
    &--comissions {
      flex-basis: 17%;
      max-width: 17%;
      display: flex;
      align-items: center;
      justify-content: flex-end; } } }
//active-campaigns END
