//input
.input {
  @include font(20px, $main-font, 400);
  line-height: 24px;
  color: $white;
  padding: 16px 10px 16px 20px;
  letter-spacing: 1px;
  background-color: rgba($black, 0.33);
  border-radius: 8px;
  border: none;
  &::placeholder {
    color: $white;
    opacity: 0.4; }
  &:active, &:focus, &:hover {
    background: $black;
    opacity: 1;
    box-shadow: none;
    color: $white; }
  @media only screen and (max-width : 991px) {
    font-size: 16px;
    padding: 14px 10px 14px 18px; }
  @media only screen and (max-width : 767px) {
    padding: 10px 10px 10px 18px; }
  &--chat {
 }    //margin-bottom: 10px
  &--search {
    background: $white;
    &::placeholder {
      color: $dark;
      opacity: 0.4; }
    &:active, &:focus, &:hover {
      background: $white;
      opacity: 1;
      box-shadow: none;
      color: $dark; } }
  &--white-long {
    width: 100%;
    background: $white;
    box-shadow: 0px 2px 4px rgba(18, 33, 45, 0.1);
    border-radius: 4px;
    padding: 15px 10px 17px 30px;
    border: 1px solid $white;
    font-size: 16px;
    line-height: 19px;
    &::placeholder {
      opacity: 0.6;
      font-size: 16px;
      line-height: 19px;
      line-height: 19px;
      font-weight: 400;
      color: $dark; }
    &:hover {
      box-shadow: 0px 2px 4px rgba(18, 33, 45, 0.1), 0px 6px 14px rgba(0, 0, 0, 0.1);
      background: $white; }
    &:active, &:focus {
      border: 1px solid $violet;
      background: $white;
      color: $dark;
      box-shadow: 0px 2px 4px rgba(18, 33, 45, 0.1);
      &::placeholder {
        opacity: 1; } } }
  &--modal {
    padding: 17px 10px 20px 20px;
    margin-bottom: 10px; } }
.search {
  display: block;
  position: relative;
  overflow: hidden;
  margin-bottom: 40px;
  &:after {
    content: "";
    display: inline-block;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translate(-30px, -50%);
    width: 23px;
    height: 23.2px;
    background: url("../img/icon/icon--lupe.svg");
    background-repeat: no-repeat;
    background-size: 100% 100%;
 } }    //background-position: center center
//input END
