//main
.main {
  background: $gray-bg-lite;
  min-height: calc(100vh - 60px - 66.4px);
  padding-top: 64px;
  position: relative;
  //border: 1px dashed red
  &--withdraw {
    width: 100%;
    padding: 65px 20px 0 20px; }
  &__block {
    &--withdraw {
      //width: 900px
      margin: 0 auto;
      width: 1160px;
      border: 1px dashed orange;
      @media only screen and (max-width : 1719px) {
        width: calc(100vw - 500px - 40px); } } } }

//main END
