//title
.section-title {
  @include font(42px, $main-font, 400);
  line-height: 50px;
  color: $dark;
  text-align: center;
  margin-bottom: 20px;
  letter-spacing: 2px;
  &--gift-cards {
    margin-bottom: 12px; }
  &--whyusebucksy {
    color: $white;
    margin-bottom: 60px; }
  @media only screen and (max-width : 991px) {
    font-size: 38px; }
  &--earn {
    text-align: left;
    //padding-top: 30px
    margin-bottom: 10px; } }
.text-label {
  font-size: 16px;
  font-weight: 500;
  line-height: 19px;
  margin-bottom: 10px;
  color: $dark;
  letter-spacing: 0.8px; }
.strikethrough-text {
  display: block;
  position: relative;
  overflow: hidden;
  text-align: center;
  margin-top: 18px;
  margin-bottom: 18px;
  &:after {
    content: "";
    position: absolute;
    display: inline-block;
    width: 100%;
    height: 1px;
    background: rgba($dark, 0.1);
    top: calc(50% + 1px);
    left: 0;
    right: 0;
    z-index: -1; }
  &__data {
    font-size: 16px;
    line-height: 16px;
    font-weight: 400;
    color: rgba($dark, 0.75);
    margin-bottom: 0;
    position: relative;
    z-index: 2;
    padding: 0 12px;
    &--bg-lite {
      background: $gray-bg-lite; } } }

//title END
