//card
.cards {
  background: $white;
  display: flex;
  flex-direction: column;
  align-items: center;
  //justify-content: space-between
  justify-content: flex-start;
  min-height: 225px;
  padding: 20px;
  border-radius: 4px;
  box-shadow: 0px 2px 4px rgba(18, 33, 45, 0.1);
  margin-bottom: 40px;
  position: relative;
  &--withdraw {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    min-height: auto; }
  &__header {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 20px; }
  &__img {
    flex: none;
    margin: 0 auto 13px auto;
    width: 100%;
    &--earn {
      width: 140px; }
    &--withdraw {
      width: 40%;
      margin-right: 20px;
      margin: 0 20px 0 0; } }
  &__title-small {
    //margin-top: auto
    font-size: 16px;
    line-height: 19px;
    color: $dark;
    text-align: center;
    margin-bottom: 0; }
  &__gallery {
    display: flex;
    flex-wrap: wrap; }
  &__thumbnail {
    flex-basis: 20%;
    max-width: 20%;
    padding: 20px 30px 20px 30px;
    &--no-padding-top {
      padding-top: 0; } } }
//card END
