//steps
.steps {
  margin-bottom: 127px;
  @media only screen and (max-width : 991px) {
    margin-bottom: 60px; } }
.steps__gallery {
  padding-top: 22px;
  margin-left: -30px;
  margin-right: -30px; }
.step {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  position: relative;
  margin-bottom: 25px;
  @media only screen and (max-width : 991px) {
    flex-wrap: wrap; }
  &--four {
    transform: translateY(-18px); } }
.step__thumbnail {
  display: flex;
  justify-content: flex-start;
  flex-basis: 50%;
  max-width: 50%;
  @media only screen and (max-width : 991px) {
    flex-basis: 100%;
    max-width: 100%; }
  &--right {
    justify-content: flex-end;
    @media only screen and (max-width : 991px) {
      justify-content: flex-start; } } }
.step__img {
  height: 362px;
  @media only screen and (max-width : 1439px) {
    width: 90%;
    height: 90%; }
  @media only screen and (max-width : 991px) {
    width: 50%;
    height: 50%;
    margin-left: auto;
    margin-right: auto; } }
.step__body {
  height: 100%;
  flex-basis: 50%;
  max-width: 50%;
  padding-left: 15px;
  padding-right: 15px;
  @media only screen and (max-width : 991px) {
    flex-basis: 100%;
    max-width: 100%; }
  &--play {
    @media only screen and (max-width : 991px) {
      order: 2; } }
  &--refer {
    @media only screen and (max-width : 991px) {
      order: 2; } } }
.step__title {
  @include font(32px, $main-font, 500);
  letter-spacing: 2px;
  line-height: 32px;
  margin-bottom: 18px;
  color: $dark;
  @media only screen and (max-width : 991px) {
    font-size: 24px; }
  &--right {
    text-align: right;
    @media only screen and (max-width : 991px) {
      text-align: left; } } }
.step__text {
  @include font(16px, $main-font, 400);
  letter-spacing: 0.8px;
  line-height: 24px;
  margin-bottom: 20px;
  color: $dark;
  @media only screen and (max-width : 991px) {
    font-size: 12px;
    letter-spacing: 0.4px;
    line-height: 18px; }
  &--right {
    text-align: right;
    @media only screen and (max-width : 991px) {
      text-align: left; } } }
.d-xs-none {
  @media only screen and (min-width : 480px) {
    display: none; } }
//steps END
