//instructions
.instructions {
  // width: 100%
  width: 1160px;
  margin: 50px auto;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  //border: 1px dashed red
  &__item {
    flex-basis: calc((100% - 80px) / 3);
    max-width: calc((100% - 80px) / 3); }
  &__card {
    padding: 20px 23px 20px 23px;
    background: $white;
    box-shadow: 0px 2px 4px rgba(18, 33, 45, 0.1);
    border-radius: 4px;
    display: flex;
    flex-direction: column;
    align-items: center; }
  &__img {
    width: 142px;
    height: 133px;
    margin: 0 auto 18px auto; }
  &__text {
    font-size: 16px;
    line-height: 26px;
    font-weight: 500;
    color: $dark;
    letter-spacing: 0.8px;
    text-align: center;
    margin-bottom: 0; } }
//instructions END
