//join-now
.join-now {
  width: 100%;
  //padding-top: 6px
  //padding-bottom: 214px
  padding-bottom: 216px;
  @media only screen and (max-width : 1023px) {
    padding-bottom: 100px; }
  @media only screen and (max-width : 767px) {
    padding-bottom: 120px; }
  @media only screen and (max-width : 479px) {
    padding-bottom: 190px; }
  &--footer {
    padding-top: 20px;
    padding-bottom: 0;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    @media only screen and (max-width : 479px) {
      height: auto;
      margin-bottom: 30px; } } }
.join-now__main-title {
  @include font(72px, $main-font, 300);
  line-height: 86px;
  color: $white;
  text-align: center;
  margin-bottom: 20px;
  letter-spacing: 3.2px;
  @media only screen and (max-width : 991px) {
    font-size: 48px;
    line-height: 48px; }
  @media only screen and (max-width : 767px) {
    font-size: 32px;
    line-height: 32px; }
  &--bold {
    font-weight: 500; }
  &--footer {
    font-size: 52px;
    line-height: 52px;
    letter-spacing: 2px;
    @media only screen and (max-width : 991px) {
      font-size: 44px;
      line-height: 44px; }
    @media only screen and (max-width : 767px) {
      font-size: 30px;
      line-height: 30px; } } }
.join-now__login-title {
  @include font(26px, $main-font, 400);
  line-height: 31px;
  color: $white;
  text-align: center;
  margin-bottom: 20px;
  letter-spacing: 1.2px;
  //margin-bottom: 35px
  margin-bottom: 33px;
  @media only screen and (max-width : 991px) {
    font-size: 20px;
    margin-bottom: 20px; }
  @media only screen and (max-width : 767px) {
    font-size: 16px;
    line-height: 16px;
    margin-bottom: 25px; }
  &--footer {
    font-size: 19px;
    line-height: 18px;
    margin-bottom: 22px;
    letter-spacing: 1.25px;
    @media only screen and (max-width : 991px) {
      font-size: 18px;
      line-height: 18px; }
    @media only screen and (max-width : 767px) {
      font-size: 14px;
      line-height: 14px; } } }
.form-inline--join-now {
  margin-bottom: 16px;
  .form-control--nickname {
    width: 410px;
    @media only screen and (max-width : 767px) {
      width: 360px;
      margin-right: 0;
      margin-bottom: 15px; }
    @media only screen and (max-width : 479px) {
      width: 260px; } }
  @media only screen and (max-width : 991px) {
    margin-bottom: 12px; } }
.form-control--nickname {
  margin-right: 16px;
  @include font(20px, $main-font, 400);
  line-height: 24px;
  color: $white;
  padding: 16px 10px 16px 20px;
  letter-spacing: 1px;
  background-color: rgba($black, 0.33);
  border-radius: 4px;
  border: none;
  &::placeholder {
    color: $white;
    opacity: 0.4; }
  &:active, &:focus, &:hover {
    background: $black;
    opacity: 1;
    box-shadow: none;
    color: $white; }
  @media only screen and (max-width : 991px) {
    font-size: 16px;
    padding: 14px 10px 14px 18px; }
  @media only screen and (max-width : 767px) {
    padding: 10px 10px 10px 18px; } }
.join-now--text {
  @include font(16px, $main-font, 500);
  line-height: 24px;
  color: $white;
  opacity: 0.3;
  margin-bottom: 0;
  letter-spacing: 0.5px;
  padding-left: 10px;
  padding-right: 0;
  @media only screen and (max-width : 767px) {
    font-size: 14px; } }
//join-now END
