//drop-down
.dropdown-menu {
  &--user-info {
    border: 1px dashed red;
    padding: 0 20px;
    width: 260px; }
  &--login {
    background: $white;
    position: absolute;
    padding: 27px 20px 20px 20px;
    right: 13px;
    margin-top: 15px;
    left: auto;
    transform: none;
    background: $gray-bg-lite;
    box-shadow: 0px 4px 8px rgba(18, 33, 45, 0.2);
    border-radius: 4px;
    border-top-right-radius: 0;
    width: 360px;
    &:after {
      position: absolute;
      top: -12px;
      right: -0.5px;
      content: "";
      display: inline-block;
      width: 21px;
      height: 16px;
      background: url("../img/icon/icon--triangle.svg");
      background-repeat: no-repeat;
      background-size: 100% 100%; } } }
.dropdown-item {
  &--user-info {
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 19px;
    font-weight: 500;
    color: $dark;
    padding: 13px 0;
    width: 220px;
    &:hover, &:active, &:focus {
      background: none;
      color: $green; } }
  &--notifications {
    //display: flex
    display: inline-block;
    padding: 13px 0;
    //flex-wrap: wrap
    width: 220px;
    border: 1px dashed pink; } }

//drop-down END
