.navbar {
  //padding: 18px 0 18px 0
  padding: 14px 0 14px 0;
  @media only screen and (max-width : 991px) {
    background: $gray-lite; }
  &--bg {
    background: $gray-lite; }
  &--dashboard {
    //padding: 10px 0
    padding: 0;
    background: $gray-lite2;
    box-shadow: 0px 2px 4px rgba(18, 33, 45, 0.1); } }
.navbar__block {
  padding: 0;
  //position: relative
  padding-left: 2px; }
.navbar-toggler {
  border: none !important;
  &.collapsed {
    border: none !important; } }
.navbar-collapse {
  &.show, &.collapsing {
    flex-basis: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: flex-start;
    @media only screen and (max-width : 991px) {
      padding: 0 15px 15px 15px;
      position: fixed;
      top: 78px;
      left: 0;
      width: 100%;
      background: $gray-lite; }
    @media only screen and (max-width : 479px) {
      flex-wrap: wrap; } } }
.navbar__top-menu {
  @media only screen and (max-width : 991px) {
    flex-basis: 50%;
    max-width: 50%; }
  @media only screen and (max-width : 479px) {
    flex-basis: 100%;
    max-width: 100%; } }
.navbar-brand {
  margin-right: 0;
  &--header {
    //position: absolute
    //left: 0
    //transform: translateX(calc(-100% - 20px))
    //transform: translateX(30px)
    margin-left: 30px;
    margin-right: 20px;
    @media only screen and (max-width : 1439px) {
      position: relative;
      transform: translateX(0);
      margin-right: 15px; }
    @media only screen and (max-width : 991px) {
      margin-right: 0; }
    @media only screen and (max-width : 767px) {
      margin-left: 15px; } }
  &--dashboard {
    padding: 0 60px; } }
.logo {
  width: 102px;
  height: 42px;
  &--footer {
    @media only screen and (max-width : 767px) {
      display: none; } } }
.navbar-toggler {
  .navbar-toggler-icon {
    color: $white;
    display: flex;
    justify-content: center;
    align-items: center; } }
.top-link {
  @include font(16px, $main-font, 500);
  letter-spacing: 0.8px;
  line-height: 24px;
  padding: 8px;
  margin-right: 4px;
  color: $white;
  &:hover, &:active, &:focus {
    text-decoration: none;
    color: $green; }
  &--footer {
    @media only screen and (max-width : 991px) {
      margin-right: 0;
      padding: 8px;
      font-size: 14px; } }
  &--align-center {
    display: flex;
    align-items: center; }
  &--mr44 {
    margin-right: 44px; }
  &--dashboard {
    opacity: 0.6;
    padding: 10px 10px;
    border-radius: 4px;
    &:hover, &:active, &:focus, &.active {
      opacity: 1;
      background: rgba($white, 0.1); } }
  &--menu-settings {
    opacity: 0.6;
    padding: 6px 22px;
    border-radius: 4px;
    margin-right: 20px;
    &:last-of-type {
      margin-right: 0; }
    &:hover, &:active, &:focus, &.active {
      opacity: 1;
      background: rgba($white, 0.1); } }
  &--green {
    color: $green;
    &:hover, &:active, &:focus, &.active {
      color: darken($green, 10%); } }
  &--modal {
    padding: 0;
    display: block;
    width: 100%;
    text-align: center; } }
.socail-links {
  position: relative;
  margin-left: auto;
  //margin-right: 2px
  padding-right: 2px;
  z-index: 5;
  &:after {
    z-index: 5;
    content: "";
    display: inline-block;
    width: 1px;
    height: 40px;
    position: absolute;
    //top: -4px
    top: 0;
    right: 0;
    background: $white;
    opacity: 0.26;
    transform: translateX(24px);
    @media only screen and (max-width : 1439px) {
      transform: translateX(10px); } }
  @media only screen and (max-width : 991px) {
    margin-left: 0;
    flex-basis: 50%;
    max-width: 50%;
    display: flex;
    flex-direction: row;
    justify-content: flex-end; }
  @media only screen and (max-width : 479px) {
    flex-basis: 50%;
    max-width: 50%;
    justify-content: flex-start; } }
.social-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 16px;
  height: 16px;
  margin-left: 4px;
  @media only screen and (max-width : 991px) {
    margin: 0 8px; } }
.user-info {
  margin-left: auto; }
.navbar-nav {
  .nav-item {
    display: flex;
    align-items: center; } }
.user-account {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;
  padding: 0 30px;
  background: $yellow;
  color: $white;
  font-size: 18px;
  line-height: 22px;
  font-weight: 500; }
