//chat
.chat {
  width: 250px;
  //position: absolute
  position: fixed;
  right: 0;
  top: 0;
  bottom: 0;
  height: 100vh;
  //height: 100%
  padding: calc(60px) 20px 20px 20px;
  background: $white;
  z-index: 100;
  box-shadow: 0px 2px 4px rgba(18, 33, 45, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  &__body {
    //border: 1px dashed red
    height: calc(100vh - 60px - 125px - 20px);
    //overflow-y: auto
    overflow-y: hidden; }
  &__footer {
    display: flex;
    flex-direction: column; }
  &__input {
    margin-bottom: 10px; }
  &__send {
    display: flex;
    align-items: center; }
  &__online-count {
    font-size: 16px;
    line-height: 16px;
    color: $dark;
    font-weight: 500;
    margin-bottom: 0;
    margin-left: 10px;
    margin-right: auto;
    &--data {
      font-weight: 600; } } }
.chat-message {
  display: flex;
  flex-direction: column;
  &__header {
    display: flex;
    justify-content: space-between; }
  &__nickname {
    flex-basis: 70%;
    max-width: 70%;
    font-size: 16px;
    line-height: 16px;
    font-weight: 500;
    color: $dark;
    margin-bottom: 0; }
  &__data {
    flex-basis: 30%;
    max-width: 30%;
    text-align: right;
    font-size: 17px;
    line-height: 17px;
    font-weight: 500;
    color: $dark;
    opacity: 0.6;
    margin-bottom: 0; }
  &__text {
    font-size: 16px;
    line-height: 26px;
    font-weight: 400;
    color: $dark; } }


//chat END
