//footer
.footer {
  height: 600px;
  width: 100%;
  background-color: $gray-lite;
  background-repeat: no-repeat;
  background-size: 100% auto;
  background-position: center top;
  background-image: url("../img/footer/footer_bg-1440.svg");
  @media only screen and (max-width : 2100px) {
    background-repeat: no-repeat;
    background-size: 100% auto;
    background-position: center top;
    //height: calc(100vw / 1440 * 586)
    height: 586px; }
  @media only screen and (max-width : 991px) {
    height: 420px;
    background-position: center top;
    background-size: 100% auto; }
  @media only screen and (max-width : 767px) {
    height: 480px; }
  @media only screen and (max-width : 479px) {
    height: 800px; } }
.footer__block {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  position: relative;
  z-index: 5;
  @media only screen and (max-width : 479px) {
    padding: 30px 0 0 0;
    justify-content: flex-end; } }
.footer__nav {
  margin-left: calc(-15px - 24px);
  margin-right: calc(-15px - 26px);
  margin-bottom: 35px;
  @media only screen and (max-width : 767px) {
    margin-left: -15px;
    margin-right: -15px; }
  @media only screen and (max-width : 479px) {
    height: auto; } }
.footer__colyright {
  @include font(14px, $main-font, 500);
  line-height: 24px;
  color: $white;
  opacity: 0.5;
  letter-spacing: 0.6px;
  margin-bottom: 14px; }
.nav--footer-left {
  justify-content: flex-start;
  @media only screen and (max-width : 767px) {
    justify-content: center; }
  @media only screen and (max-width : 479px) {
    flex-direction: column;
    margin: 0 auto;
    align-items: center; } }
.nav--footer-right {
  justify-content: flex-end;
  @media only screen and (max-width : 767px) {
    justify-content: center; }
  @media only screen and (max-width : 479px) {
    flex-direction: column;
    align-items: center; } }
//footer END
