//create-campain
.create-campain {
  //width: 100%
  width: 1160px;
  margin: 50px auto;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  &__item {
    &--name {
      flex-basis: calc((100% - 40px) / 3 * 1.42);
      max-width: calc((100% - 40px) / 3 * 1.42 ); }
    &--code {
      flex-basis: calc((100% - 40px) / 3 * 0.58);
      max-width: calc((100% - 40px) / 3 * 0.58);
      padding-right: 13px; }
    &--create {
      padding-left: 13px;
      flex-basis: calc((100% - 40px) / 3);
      max-width: calc((100% - 40px) / 3); } } }

//create-campain END
