//history
.history {
  width: 100%;
  //width: 1160px
  //width: 960px
  background: $white;
  border-radius: 4px;
  padding: 28px 20px 20px 20px;
  box-shadow: 0px 2px 4px rgba(18, 33, 45, 0.1);
  //border: 2px dashed orange
  //margin: 50px auto 50px
  &__header {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 18px; }
  &__title {
    font-size: 16px;
    line-height: 19px;
    margin-bottom: 0;
    color: $dark;
    font-weight: 500;
    letter-spacing: 0.5px;
    flex-basis: 30%;
    max-width: 30%;
    &--videos {
      flex-basis: 32%;
      max-width: 32%; }
    &--brand {
      flex-basis: 26.5%;
      max-width: 26.5%;
      padding-left: 42px; }
    &--time {
      flex-basis: 28%;
      max-width: 28%;
      padding-left: 78px; }
    &--profit {
      flex-basis: 13.5%;
      max-width: 13.5%;
      text-align: right;
      padding-right: 30px; } }
  &__body {
    display: flex;
    flex-direction: column;
    width: 100%; }
  &__row {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    &:nth-child(odd) {
      background: rgba($dark, 0.06);
      border-radius: 4px; } }
  &__item {
    //border: 1px dashed red
    display: flex;
    align-items: center;
    padding: 15.2px 18px;
    &--videos {
      flex-basis: 32%;
      max-width: 32%; }
    &--brand {
      flex-basis: 26.5%;
      max-width: 26.5%; }
    &--time {
      flex-basis: 28%;
      max-width: 28%; }
    &--profit {
      flex-basis: 13.5%;
      max-width: 13.5%; } }
  &__text {
    margin-bottom: 0;
    line-height: 1;
    font-size: 16px;
    line-height: 19px;
    color: $dark;
    letter-spacing: 0.7px;
    font-weight: 500; } }
//history END
