//sidebar
.sidebar {
  width: 250px;
  //position: absolute
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  //min-height: 100vh
  //height: 100%
  height: 100vh;
  padding: calc(60px + 55px) 10px 20px 10px;
  background: $dark-bg;
  z-index: 100;
  box-shadow: 0px 2px 4px rgba(31, 3, 105, 0.102185);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  &__main-title {
    font-size: 42px;
    line-height: 42px;
    color: $white;
    font-weight: 400;
    margin: 0 0 40px 10px; }
  &__menu-title {
    font-size: 20px;
    line-height: 20px;
    color: $white;
    font-weight: 500;
    margin: 0 0 20px 10px; }
  &--footer {
    padding: 0 0 29px 0; } }
//sidebar END
