//btn
.btn {
  border-radius: 4px;
  border: none;
  &:hover, &:active, &:focus, &.active {
    background: none;
    opacity: 0.9;
    border: none;
    box-shadow: none; }
  &--login {
    @include font(16px, $main-font, 600);
    color: $white;
    line-height: 24px;
    background: none;
    letter-spacing: 1px;
    border: none;
    opacity: 1;
    &:after {
      margin-left: 1px;
      border-top-width: 4px;
      border-left-width: 4px;
      border-right-width: 4px; }
    &:hover,&:active, &:focus {
      color: $green; } }
  &--language {
    background: none;
    margin-left: 9px;
    color: $white;
    &:after {
      margin-left: 1px;
      border-top-width: 4px;
      border-left-width: 4px;
      border-right-width: 4px; }
    &:hover,&:active, &:focus {
      &:after {
        color: $green; } }
    @media only screen and (max-width : 991px) {
      margin-left: 0;
      padding-left: 0; } }
  &--join-now {
    @include font(20px, $main-font, 700);
    line-height: 24px;
    color: $white;
    padding: 15px 26px;
    background: $green;
    letter-spacing: 0.8px;
    &:hover,&:active, &:focus {
      background: $green-lite; }
    @media only screen and (max-width : 991px) {
      font-size: 16px;
      padding: 13px 26px; }
    @media only screen and (max-width : 767px) {
      width: 360px;
      font-size: 18px;
      padding: 10px; }
    @media only screen and (max-width : 479px) {
      width: 260px; } }
  &--user-info {
    background: none;
    display: flex;
    align-items: center;
    font-size: 18px;
    line-height: 22px;
    color: $white;
    &:hover, &:active, &:focus {
      color: $green; } }
  &--notriangle {
    &:after {
      display: none; } }
  &--chat-setting {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    width: 46px;
    height: 36px;
    background: rgba($dark, 0.1); }
  &--chat-send {
    font-size: 16px;
    font-weight: 600;
    color: $white;
    background: $violet;
    letter-spacing: 0.5px;
    &:hover,&:active, &:focus {
      background: darken($violet, 10%); } }
  &--connections {
    width: 120px;
    height: 36px;
    border-radius: 4px;
    background: $violet;
    margin-left: auto;
    color: $white;
    position: relative;
    &:after {
      content: "Go";
      position: absolute;
      color: $white;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      font-size: 16px;
      font-weight: 600; }
    &:hover {
      background: darken($violet, 10%); }
    &.active, &:focus, &:active {
      background: $green;
      &:after {
        content: "";
        width: 16px;
        height: 16px;
        display: inline-block;
        background-image: url("../img/icon/icon--check-white.svg");
        background-repeat: no-repeat;
        background-size: 100% 100%; } } }
  &--violet-long {
    border-radius: 4px;
    width: 100%;
    height: 58px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: $violet;
    color: $white;
    font-size: 16px;
    font-weight: 600;
    letter-spacing: 0.8px;
    &:hover, &.active, &:focus, &:active {
      background: darken($violet, 10%); } }
  &--switch-gray {
    width: 72px;
    height: 36px;
    border-radius: 4px;
    background-color: rgba($dark, 0.2);
    position: relative;
    &:after {
      content: "";
      display: inline-block;
      width: 28px;
      height: 28px;
      border-radius: 4px;
      background: $dark;
      position: absolute;
      left: 4px;
      top: 4px; }
    &:hover, &:focus {
      background-color: rgba($dark, 0.4); }
    &.active, &:active {
      background-color: $green;
      &:after {
        left: auto;
        right: 4px;
        background: $white; } } }
  &--modal {
    border-radius: 4px;
    width: 320px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    font-weight: 600;
    letter-spacing: 0.8px;
    margin-bottom: 10px;
    position: relative; }
  &--green {
    background: $green;
    color: $white;
    &:hover, &.active, &:focus, &:active {
      background: darken($green, 10%); } }
  &--blue-link {
    background: $blue-link;
    color: $white;
    &:hover, &.active, &:focus, &:active {
      background: darken($blue-link, 10%);
      color: $white; } }
  &--blue-dark {
    background: $blue-dark;
    color: $white;
    &:hover, &.active, &:focus, &:active {
      background: darken($blue-dark, 10%);
      color: $white; } } }
//btn END
