//whyusebucksy
.whyusebucksy {
  background: $violet;
  padding-top: 64px;
  padding-bottom: 60px; }
.whyusebucksy__data {
  text-align: center; }
.whyusebucksy__count {
  @include font(42px, $main-font, 400);
  line-height: 42px;
  color: $green;
  margin-bottom: 5px;
  display: flex;
  justify-content: center;
  flex-wrap: nowrap;
  margin-bottom: 12px;
  @media only screen and (max-width : 1279px) {
    font-size: 36px;
    line-height: 42px; }
  @media only screen and (max-width : 991px) {
    font-size: 30px;
    margin-bottom: 6px; }
  &--data {
    letter-spacing: 20px;
    margin-right: -20px;
    position: relative;
    @media only screen and (max-width : 1279px) {
      letter-spacing: 10px;
      margin-right: -10px; } }
  &--wrap {
    background: $white;
    border-radius: 4px;
    margin: 0 4px;
    padding: 2px 10px 4px 10px;
    min-width: 40px;
    box-shadow: 0px 2px 4px rgba(209, 209, 209, 0.5); } }
.whyusebucksy__label {
  @include font(16px, $main-font, 400);
  line-height: 24px;
  opacity: 0.75;
  color: $white;
  letter-spacing: 0.8px;
  @media only screen and (max-width : 991px) {
    margin-bottom: 25px; } }
//whyusebucksy END
